<template>
  <div class="app-Serialization-list">
    <div class="mui-slider">
      <div class="mui-slider-group">
       
      </div>
    </div>
  </div>
</template>
<style>
.app-Serialization-list .mui-table-view .mui-table-view-cell {
  width: 100%;
  display: inline-block;
  line-height: 80px;
  height: 80px;
  text-align: left;
}
.app-Serialization-list .mui-table-view .mui-table-view-cell > a {
  height: 100%;
  width: 20%;
}

.app-Serialization-list .mui-table-view .mui-table-view-cell .mui-media-body {
  height: 100%;
  width: 80%;
  float: right;
}

.app-Serialization-list .mui-table-view .mui-table-view-cell:after {
  height: 0;
}
</style>

<script>
export default {
  data() {
    return {};
  },
  mounted() {
    var gallery = mui(".mui-slider");
    gallery.slider({
      interval: 0 //自动轮播周期，若为0则不自动播放，默认为0；
    });
  }
};
</script>



